import { MouseEventHandler, ReactNode } from "react"
import { Box, Button, Tooltip, TooltipProps, Typography } from "@rp/react/web/mui/core"

type Props = {
  title: string
  description: ReactNode
  placement?: TooltipProps["placement"]
  maxWidth?: number
  onDone?: MouseEventHandler
  onClose?: MouseEventHandler
  children: TooltipProps["children"]
  offsetX?: number
  offsetY?: number
  open?: boolean
}

const WatchlistTutorialTooltip = ({
  title,
  description,
  placement,
  children,
  maxWidth = 310,
  offsetX = 0,
  offsetY = 0,
  open,
  onDone,
  onClose,
}: Props) => {
  return (
    <Tooltip
      componentsProps={{
        popper: { modifiers: [{ name: "offset", options: { offset: [offsetX, offsetY] } }] },
        tooltip: { sx: { p: 2.5, maxWidth, borderRadius: "8px" } },
      }}
      open={open}
      placement={placement}
      arrow
      title={
        <Box
          onClick={ev => {
            ev.stopPropagation()
            ev.preventDefault()
          }}
        >
          <Typography sx={{ fontSize: 20, fontWeight: 700, mb: 1.5, color: "text.primary" }}>
            {title}
          </Typography>
          {description}
          <Box
            sx={{
              mt: 2.5,
              display: "flex",
              alignItems: "center",
              gap: 1,
              "& button": { px: 2, lineHeight: "23px" },
            }}
          >
            {onClose && (
              <Button onClick={onClose} size="small" variant="text-outlined-mono">
                Close
              </Button>
            )}
            <Button onClick={onDone} size="small" variant="contained">
              Add Column
            </Button>
          </Box>
        </Box>
      }
    >
      {children}
    </Tooltip>
  )
}

export { WatchlistTutorialTooltip }
